var nge = require("./engine.js");
var els = require("./els.js");
var Class = nge.Class;
var ElsTouch = nge.Model.extend({
    ctor : function(game) {
        this.game = game;
        //,touchBegan
        //
        this._firstTouchPoint = {x:0, y:0};
        //
        //touchBegantouchMove,dx,dy
        this._lastTouchPoint = {x:0, y:0};
        //drag
        this._lastPointDrag = {x:0, y:0};
        this._isNeedDrop = false;
        this._isNeedHold = false;
        //
        this._maxRLAct = 0;
        //
        this._maxDAct = 0;
        this._firstTouchTime = 0;
        this._lastMoveTime = 0;     //
        this._moveDX = 0;           //x
        this._moveDY = 0;           //y
        this._idenSizeX = 0;        //
        this._idenSizeY = 0;        //
        this._idenSizeDrop = 0;

        this._btn_scope = {
            'btn_fightAI' : cc.rect(300, 640, 320, 110), //
            'btn_classic' : cc.rect(300, 500, 320, 110), //
            'btn_pause' : cc.rect(15, 1220, 45, 45),     //
            'btn_help'  : cc.rect(430, 190, 70, 70),     //
            'btn_rank' : cc.rect(620, 190, 70, 70),      //
            'btn_share' : cc.rect(520, 190, 70, 70),     //
            'btn_continue' : cc.rect(180, 700, 370, 140), //
            'btn_back_pause' : cc.rect(180, 550, 370, 140), //
            'btn_reStart' : cc.rect(180, 500, 360, 150),   //
            'btn_back_lose' : cc.rect(280, 450, 160, 100),  //:
            'btn_nextStage' : cc.rect(300, 440, 210, 120),    //
            'btn_ShareFlaunt' : cc.rect(180, 520, 340, 100), //
            'btn_back_win' : cc.rect(280, 470, 160, 60),     //
            'btn_help_back' : cc.rect(570, 915, 60, 60),        //
            'btn_emergencyShare' : cc.rect(600, 1020, 100, 121), //5
            'btn_emergencyShareS' : cc.rect(600, 800, 100, 121),  //5
            'btn_keepstar_share' : cc.rect(210, 530, 300, 100),  //: 
            'btn_keepstar_cancel' : cc.rect(290, 420, 180, 90), //: 
            'btn_rank_close' : cc.rect(565, 1018, 50, 50),
            'btn_rank_challenge' : cc.rect(93, 990, 257, 85),
            'btn_rank_classic' : cc.rect(350, 990, 257, 85),
        };
    },

    triggerPutUserAction : function(a) {
        //console.log("PUTACT...."+a);
        this.game.useract[this.game.useract.length] = a;
    },

    began : function(x, y) {
        return this._beganGesture(x, y);
    },

    moved : function(x, y) {
        this._movedGesture(x, y);
    },

    ended : function(x, y) {
        this._endedGesture(x, y);
    },

    _setTouchKeyThreshold : function(dx, dt) {
        this._idenSizeX = 48;
        this._idenSizeY = 40;
        this._idenSizeDrop = 35;
    },

    _beganGesture : function(x, y) {
        var now = new Date().getTime();
        this._lastMoveTime = now;
        this._firstTouchTime = now;
        this._firstTouchPoint.x = x;
        this._firstTouchPoint.y = y;
        this._lastTouchPoint.x = x;
        this._lastTouchPoint.y = y;
        this._lastPointDrag.x = x;
        this._lastPointDrag.y = y;
        this._isNeedDrop = false;
        this._isNeedHold = false;
        this._moveDX = 0;
        this._moveDY = 0;
        this._maxRLAct = this.game.model.mgrid[0].mcore.cur_x - 5;
        return true;
    },

    _movedGesture : function(x, y) {
        var currenttime = new Date().getTime();

        //
        if (Math.abs(this._firstTouchPoint.x - x) > this._moveDX)
            this._moveDX = Math.abs(this._firstTouchPoint.x - x);
        if (Math.abs(this._firstTouchPoint.y - y) > this._moveDY)
            this._moveDY = Math.abs(this._firstTouchPoint.y - y);

        //dxdydx_drag, dy_drag
        var dx = x - this._lastTouchPoint.x;
        var dy = y - this._lastTouchPoint.y;
        this._lastTouchPoint.x = x;
        this._lastTouchPoint.y = y;
        var dx_drag = x - this._lastPointDrag.x;
        var dy_drag = y - this._lastPointDrag.y;

        var dt = currenttime - this._lastMoveTime;
        this._setTouchKeyThreshold(dx, dt);

        if(dx==0 && dy==0) return;

        //console.log("TOUCHMOVE...dx="+dx+" dy="+dy+" dxdrag="+dx_drag+" dydrag="+dy_drag);

        //
        if (dy < -this._idenSizeDrop) {
            if (dx <= this._idenSizeX * 0.6 && dx >= -this._idenSizeX * 0.6)
                this._isNeedDrop = true;
        }

        //
        if (dy > this._idenSizeDrop) {
            if (dx <= this._idenSizeX * 1.1 && dx >= -this._idenSizeX * 1.1)
                this._isNeedHold = true;
            this._lastPointDrag.y = y;
        }

        //
        if (dy >= -this._idenSizeDrop && dy <= this._idenSizeDrop) {
            var horizontalMove = false;
            if (dy >= 0)
                horizontalMove = true;
            else if (Math.abs(dx) / Math.abs(dy) > 1.0)
                horizontalMove = true;
            else
                this._lastPointDrag.x = x;

            //
            if (dx_drag > this._idenSizeX && horizontalMove) {
                var actCount = parseInt(dx_drag / this._idenSizeX);

                for (var i = 0; i < actCount; i++) {
                    if (this._maxRLAct <= 5) {
                        this.triggerPutUserAction('R');
                        this._maxRLAct++;
                    }
                }
                this._lastPointDrag.x += this._idenSizeX * actCount;
                this._lastPointDrag.y = y;

                this._isNeedDrop = false;
                this._isNeedHold = false;
            }
            //
            if (dx_drag < -this._idenSizeX && horizontalMove) {
                var actCount = parseInt(Math.abs(dx_drag) / this._idenSizeX);
                for (var i = 0; i < actCount; i++) {
                    if (this._maxRLAct >= -5) {
                        this.triggerPutUserAction('L');
                        this._maxRLAct--;
                    }
                }
                this._lastPointDrag.x -= this._idenSizeX * actCount;
                this._lastPointDrag.y = y;

                this._isNeedDrop = false;
                this._isNeedHold = false;
            }

            //
            if (dy_drag < -this._idenSizeY) {
                var actCount = parseInt(Math.abs(dy_drag) / this._idenSizeY);
                for (var i = 0; i < actCount; i++) {
                    if (this._maxDAct <= 20) {
                        this.triggerPutUserAction('D');
                        this._maxDAct++;
                    }
                }
                this._lastPointDrag.x = x;
                this._lastPointDrag.y -= this._idenSizeY * actCount
            }
        }

        this._lastMoveTime = currenttime;
    },

    _endedGesture : function(x, y) {

        if(!this._checkTriggerBtnClick(x, y)){
            if(this._isNeedDrop) {
                this.triggerPutUserAction('W');
                this._isNeedDrop=false;
            }
            if(this._isNeedHold) {
                this.triggerPutUserAction('S');
                this._isNeedHold=false;
            }
            //
            if(this._moveDX<=25.0 && this._moveDY<=25.0)
            {
                this.triggerPutUserAction('T');
            }
        }
    },

    _checkTriggerBtnClick : function (_x, _y) {
        //return this.game.checkTriggerBtnClick(this._btn_scope,  new cc.Vec2(_x, _y));
        return false; //
    }
});
(function (window, factory) {
    if (typeof exports === 'object') {
        module.exports = factory();
    } else if (typeof define === 'function' && define.amd) {
        define(factory);
    } else {
        window.ElsTouch = factory();
    }
})(this, function () {
    return ElsTouch
});
