    editBoxTextDidEnd: function(event) {
        console.log(event);
        var text = event.string;
        var reg = new RegExp("^[\\u4E00-\\u9FFF]+$", "g");
        if (!reg.test(text)) {
            wx.showToast({
                title: '',
                duration: 2000,
                icon: null
            });
            event.getComponent('cc.EditBox').string = "";
            return;
        }
        if (text.length > 6) {
            text = text.slice(0, 6);
        }
        event.getComponent('cc.EditBox').string = text;
    },

